






















import { Vue, Component } from 'vue-property-decorator';
import { mapGetters } from 'vuex';
import axios from 'axios';
import { BootstrapVue, BootstrapVueIcons } from 'bootstrap-vue';

interface DaumDay {
  accTradePrice: number; // 
  accTradeVolume: number; // 
  change: string; // ?
  changePrice: number; //  (  )
  changeRate: number; // 
  date: string; // 
  exchangeCountry: string; // ?
  highPrice: number; // 
  listedSharesCount: string;
  lowPrice: number; // 
  openingPrice: number; // 
  periodTradePrice: number; // 
  periodTradeVolume: number; // 
  prevClosingPrice: number; // 
  symbolCode: string; //  
  tradePrice: number; //  
  tradeTime: string; // 
}

Vue.config.productionTip = false
Vue.use(BootstrapVue);
Vue.use(BootstrapVueIcons);

const DAUM = 'https://finance.daum.net';
const KRX = 'http://data.krx.co.kr';
const PROXY = 'https://yg01.herokuapp.com/';
const PROXY_ORIGIN = 'https://yg01.herokuapp.com/';
const HTTP = {
  daum: axios.create({
    baseURL: PROXY + DAUM,
    headers: {
      Accept: 'application/json'
    }
  }),
  krx: axios.create({
    baseURL: PROXY + KRX,
    headers: {
      Accept: 'application/json',
      Origin: PROXY_ORIGIN,
    }
  }),
};
const FIELDS = [
  { key: 'accTradePrice', label: '' },
  { key: 'accTradeVolume', label: '' },
  { key: 'change', label: '?' },
  { key: 'changePrice', label: '' },
  { key: 'changeRate', label: '' },
  { key: 'date', label: '' },
  { key: 'exchangeCountry', label: '?' },
  { key: 'highPrice', label: '' },
  { key: 'listedSharesCount', label: '' },
  { key: 'lowPrice', label: '' },
  { key: 'openingPrice', label: '' },
  { key: 'periodTradePrice', label: '' },
  { key: 'periodTradeVolume', label: '' },
  { key: 'prevClosingPrice', label: '' },
  { key: 'symbolCode', label: ' ' },
  { key: 'tradePrice', label: '' },
  { key: 'tradeTime', label: '' },
];

@Component({
  components: {
  },
  computed: mapGetters({
  }),
})
export default class DaumDays extends Vue {
  code:string = 'A303530';
  daumDays: DaumDay[] = [];
  fields: any = FIELDS;
  isBusy: boolean = false;

  async onClick() {
    const PARAM: any = {
      symbolCode: this.code,
      page: 1,
      perPage: 100,
      pagination: true,
    };
    this.isBusy = true;
    this.daumDays = (await HTTP.daum.get(`/api/quote/${this.code}/days`, {params: PARAM})).data.data;
    this.isBusy = false;
  }

  onClear() {
    this.daumDays = [];
  }
}
