import { mapGetters } from 'vuex';

import DaumTimes from '@/components/Button/DaumTimes.vue'
import { forEach } from 'lodash';
// import { forEach } from 'lodash';

const FIELDS = [
  { key: 'name', label: '' },
  { key: 'accTradePrice', label: '' },
  { key: 'todayVolume', label: ' ' },
  { key: 'yesterdayVolume', label: ' ' },
  { key: 'accTradeVolume', label: '' },
  /* { key: 'change', label: '?' }, */
  { key: 'changePrice', label: '' },
  { key: 'changeRate', label: '' },
  /* { key: 'date', label: '' }, */
  /* { key: 'exchangeCountry', label: '?' }, */
  /* { key: 'highPrice', label: '' }, */
  /* { key: 'listedSharesCount', label: '' }, */
  /* { key: 'lowPrice', label: '' }, */
  /* { key: 'openingPrice', label: '' }, */
  /* { key: 'periodTradePrice', label: '' }, */
  /* { key: 'periodTradeVolume', label: '' }, */
  /* { key: 'prevClosingPrice', label: '' }, */
  /* { key: 'symbolCode', label: ' ' }, */
  { key: 'yesterdayTradePrice', label: ' ' },
  { key: 'tradePrice', label: '' },
  { key: 'tradeTime', label: '' },
];

@Component({
 components: {
   DaumTimes,
 },
 computed: mapGetters({
   selected: 'common/getSelected',
 }),
})
export default class Home extends Vue {
  selected!: any;

  fields: any = FIELDS;

  daumTime: any = null;
  daumTimes: any = [];

  options: any = [];

  datas: any = [];

  @Watch('selected', { deep: true })
  onSelected(newVal: any) {
    this.options = [];
    forEach(newVal.codes, (code) => {
      this.options.push({
        value: code.symbolCode,
        text: code.name,
      });
    });
  }

  onComparision(el: any) {
    let apples:any = [];
    apples = this.datas.filter((element: any) => {
      if(element.name != el.name)  {
        return true;
      }
    });

    if(apples == undefined) {
      apples = [];
    }

    apples.push(el);
    apples.sort(function (a: any, b: any) {
      return b.accTradeVolume - a.accTradeVolume;
    });

    this.datas = apples;
  }


  // async mounted() {
  //   const res = (await this.$store.dispatch('common/krxKospi')).data.OutBlock_1;

  //   forEach(res, (re) => {
  //     this.options.push({
  //       value: re.symbolCode,
  //       text: re.name,
  //     });
  //   });
  // }
}
